<script src="../../config/optionsIm.js"></script>
<template>
  <a-layout>
    <div class="register">
      <div class="main">
        <div class="input-box">
          <div class="theme">
            <h3></h3>
          </div>
          <!--  -->
          <div class="userId">
            <a-input placeholder="" v-model="userId">
              <a-icon slot="prefix" type="user"/>
            </a-input>
          </div>
          <!--  -->
          <div class="passwd">
            <a-input-password
                placeholder=""
                v-model="password"
            >
              <a-icon slot="prefix" type="lock"/>
            </a-input-password>
          </div>
          <!--  -->
          <div class="phoneNum">
            <a-input
                placeholder=""
                v-model="phoneNum"
            >
              <a-icon slot="prefix" type="phone"/>
            </a-input>
          </div>

          <!--  -->
          <div class="verification-code">
            <a-input
                class="verificationCode"
                type="text"
                placeholder=""
                :style="{width:'55%'}"
                v-model="phoneSMSCode"
            >
              <a-icon slot="prefix" type="mobile"/>
            </a-input>
            <!--  -->
            <a-button
                type="primary"
                v-show="countdownBtn"
                @click="getSMSCode"
            >
              
            </a-button>
            <a-button
                type="primary"
                v-show="!countdownBtn"
                disabled
            >
              {{ countdown }}
            </a-button>
          </div>

          <!--  -->
          <div class="register-btn">
            <a-button type="primary" class="register-btn" @click="userRegister"
            >
            </a-button
            >
          </div>

          <div class="to-login">
            <span @click="$router.push('/login')" style="cursor: pointer"></span>
          </div>
        </div>

        <!--  -->
        <AWord></AWord>
      </div>
    </div>
  </a-layout>
</template>

<script>
// 
import "./RegisterView.scss";
import cookie from "vue-cookie";
import _ from 'lodash'
import {mapState, mapMutations} from "vuex";
import {regUser} from "@/config/optionsIm.js"
import {getAdminToken, getSMS} from "@/api/axiosGetData";

import AWord from "@/components/AWord/AWord";

export default {
  name: "RegisterView",
  components: {AWord},
  data() {
    return {
      // Id
      userId: "",
      // 
      password: "",
      // 
      phoneNum: null,
      // 
      countdown: 60,
      // 
      countdownBtn: true,
      // 
      ranomNum: _.random(1000, 9999),
      // 
      phoneSMSCode: null,
    };
  },
  computed: {
    ...mapState(['registerCode'])
  },
  watch: {
    registerCode(nV) {
      if (nV == 200) {
        this.$message.success('')
        setTimeout(()=>{
          this.$router.push('/')
        },500)
      }
      if (nV == 400){
        this.$message.error('')
      }
      setTimeout(()=>{
        this.$store.state.registerCode = 0
      },100)
    }
  },
  created() {
    // tokenindex
    if (cookie.get('token')) {
      this.$router.push(`/index`)
    }
    if (this.$route.query.state) {
      this.$message.error(',!')
    }
  },
  mounted() {
    if (cookie.get('token')) {
      this.$router.push('/index')
      console.log('');
    }
    getAdminToken()
  },

  methods: {
    //
    getSMSFunc() {
      if (/^(?:(?:\+|00)86)?1[3-9]\d{9}$/.test(this.phoneNum)) {
        this.counDownNum()
        getSMS(this.phoneNum, this.ranomNum)
      } else {
        this.$message.error('');
      }
    },
    //
    getSMSCode: _.throttle(function () {
      this.getSMSFunc()
    }, 1000),
    // 
    counDownNum() {
      console.log('counDownNum')
      this.countdownBtn = false
      let timer = setInterval(() => {
        if (this.countdown > 0) {
          this.countdown--
        }
        if (this.countdown == 0) {
          console.log('countdown End')
          this.countdownBtn = true
          this.countdown = 60
          clearInterval(timer)
        }
      }, 1000)
    },
    // 
    userRegister() {
      if (this.userId && this.password && this.phoneNum && this.ranomNum == this.phoneSMSCode) {
        regUser(this.userId, this.password)
      } else {
        this.$message.error('');
      }
    },
    ...mapMutations(["setToken"]),
  },
};
</script>