<template>
  <a-layout>
    <div class="login">
      <div class="main">
<!--        -->
        <div class="input-box">
          <div class="logo">
            <h3></h3>
          </div>
          <div class="username">
            <a-input placeholder="" v-model="userId" @keydown.enter="login">
              <a-icon slot="prefix" type="user"/>
            </a-input>
          </div>
          <div class="passwd">
            <a-input-password placeholder="" v-model="password" @keydown.enter="login">
              <a-icon slot="prefix" type="lock"/>
            </a-input-password>
          </div>
          <div class="login-btn">
            <a-button type="primary" class="login-btn" @click="login"></a-button>
          </div>
          <div class="to-register">
            <span @click="$router.push('/register')" style="cursor: pointer"></span>
          </div>
        </div>

<!--        -->
<!--        <div class="a-word">
          <div class="text">
            <p>{{ hitokoto }}</p>
            <span>&#45;&#45;{{ author }}</span>
          </div>
        </div>-->
        <AWord></AWord>
      </div>
    </div>
  </a-layout>
</template>

<script>
import './LoginView.scss'
import {getUserConfig,getGoodFriends,getGroups,blackFriends} from '@/config/optionsIm.js'
import cookie from 'vue-cookie'
import {mapMutations} from 'vuex'
import AWord from "@/components/AWord/AWord";

export default {
  name: "loginView",
  data() {
    return {
      userId: '',
      password: '',
    }
  },
  components:{
    AWord
  },
  created() {
    // tokenindex
    if (cookie.get('token')){
      this.$router.push(`/index`)
    }
    if (this.$route.query.state){
      this.$message.error(',!')
    }
  },
  methods: {
    login() {
      getUserConfig(this.userId, this.password)
      // setTimeouttoken
      setTimeout(() => {
        // tokenvuex
        this.setToken(cookie.get('token'))
        if (cookie.get('token')){
          this.$message.success('')
        }else {
          this.$message.error(',')
        }

        // 
        if (this.$route.query.state) {
          this.goBack()
          return
        }
        this.$router.push(`/index`)
        getGoodFriends()
        getGroups()
        blackFriends()
      }, 500)
    },
    // 
    goBack() {
      setTimeout(() => {
        this.$router.push(this.$route.query.redirect)
      }, 500)
    },
    ...mapMutations(["setToken"]),
  }
}
</script>
