<template>
  <div>
    <a-modal v-model="Groupvisible" :title="title" :afterClose="afterClose" >
      <div v-if="GroupModalstatus === 1">
        <p><a-input placeholder="" v-model="Groupname"/></p>
        <p><a-textarea
            v-model="txt"
            placeholder=""
            :auto-size="{ minRows: 3, maxRows: 5 }"
        /></p>

        <span></span>
        <p>
          <a-radio-group v-model="value" @change="onChange">
            <a-radio :value="1">
              
            </a-radio>
            <a-radio :value="2">
              
            </a-radio>
          </a-radio-group>
        </p>

        <span></span>
        <p>
          <a-radio-group v-model="value2" @change="onChange">
            <a-radio :value="1">
              
            </a-radio>
            <a-radio :value="2">
              
            </a-radio>
          </a-radio-group>
        </p>
      </div>

      <template #footer>
        <a-button type="primary" @click="creatGroupOrRoom">
         
        </a-button>
      </template>
    </a-modal>
  </div>
</template>


<!-- /-->
<script>
import './EstabIish.scss'
import {SetupGroup} from "@/config/optionsIm";

export default {
  name: "EstabIish",
  data() {
    return {
      Groupvisible: false,
      title:null,
      //
      txt:'',
      //
      value: 1,
      //
      value2:1,
      // 
      Groupname:''
    }
  },
  props:['GroupStatus','GroupModalstatus'],
  watch:{
    GroupStatus(nV){
      this.Groupvisible = nV
    },
    GroupModalstatus(nV){
      if (nV === 1){
        this.title = ""
      }else if (nV === 2){
        this.title = ""
      }
    }
  },
  methods:{
    afterClose(){
      this.Groupvisible = false
      this.$emit('grouphandle', this.Groupvisible)
    },
    onChange(e) {
      console.log('radio checked', e.target.value);
    },
    creatGroupOrRoom(){
      if (this.GroupModalstatus === 1){
      //  
        if (this.Groupname.trim()){
          SetupGroup(this.Groupname,this.txt,this.value,this.value2)
          this.$message.success('');
          this.Groupvisible = false
        }else {
          this.$message.error('');
        }
      }
    }
  }
}
</script>

