<template>
  <div>
    <a-drawer
        title=""
        :width="360"
        :visible="visible"
        :body-style="{ paddingBottom: '80px' }"
        @close="onClose"
    >
      <a-form :form="form" layout="vertical" hide-required-mark>
        <a-row :gutter="16">
          <a-col :span="20">
<!--            -->
            <a-form-item label="">
              <a-input
                  placeholder=""
                  v-model="nickname"
              />
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="16">
          <a-col :span="20">
<!--            id-->
            <a-form-item label="ID">
              <a-input :disabled="true" v-model="this.$store.state.userId"/>
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="16">
          <a-col :span="20">
<!--            -->
            <a-form-item label="">
              <a-input
                  placeholder="" v-model="signature"
              />
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="16">
          <a-col :span="20">
<!--            -->
            <a-form-item label="">
              <a-select
                  placeholder=""
                  v-model="sex"
              >
                <a-select-option value="1">
                  
                </a-select-option>
                <a-select-option value="2">
                  
                </a-select-option>
              </a-select>
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="16">
          <a-col :span="20">
<!--            -->
            <a-form-item label="">
              <a-input
                  placeholder="" v-model="phoneNumber"
              />
            </a-form-item>
          </a-col>
        </a-row>
        <a-row :gutter="16">
          <a-col :span="20">
            <!--            -->
            <a-form-item label="">
              <a-date-picker
                  style="width: 100%"
                  :get-popup-container="trigger => trigger.parentNode"
                  v-model="dateOfBirth"
              />
            </a-form-item>
          </a-col>
        </a-row>
      </a-form>
      <div
          :style="{
          position: 'absolute',
          right: 0,
          bottom: 0,
          width: '100%',
          borderTop: '1px solid #e9e9e9',
          padding: '10px 16px',
          background: '#fff',
          textAlign: 'right',
          zIndex: 1,
        }"
      >
        <a-button :style="{ marginRight: '8px' }" @click="onClose">
          
        </a-button>
        <a-button type="primary" @click="submit">
          
        </a-button>
      </div>
    </a-drawer>
  </div>
</template>

<script>
import './PersonalInformation.scss'
import {setUserInfo} from '@/config/optionsIm'
import {mapState} from "vuex";

export default {
  name: "PersonalInformation",
  data(){
    return {
      form: this.$form.createForm(this),
      visible: false,
      nickname: "" ,
      signature: "",
      sex:"",
      phoneNumber:  "",
      dateOfBirth: ""
    }
  },
  watch: {
    show(nV){
      this.visible = nV
    },
    userInfo(){
      this.nickname = this.userInfo.nickname
      this.signature = this.userInfo.sign
      this.sex = this.userInfo.gender
      this.phoneNumber = this.userInfo.phone
      this.dateOfBirth = this.userInfo.birth
    }
  },
  computed:{
    ...mapState(["userInfo"])
  },
  props:['show'],
  methods:{
    // 
    onClose() {
      this.visible = false;
      this.$emit('onclose',this.visible)
    },
    // 
    submit(){
      let opt = {
        nickname:this.nickname,
        sign:this.signature,
        gender:this.sex,
        phone:this.phoneNumber,
        birth:this.dateOfBirth._d
      }
      setUserInfo(opt)
      this.$message.success('')
      this.onClose()
    }
  },
}
</script>

<style scoped>

</style>