//
export let sendMessage = (msg, chatType, ID, useName,status) => {

//msg   chatType   ID 
         let option = {
             type: "txt",
             msg: msg,
             from: useName,
             to: ID,
             chatType: chatType,
             ext:{
                 key1:status
             }
         }

    let Msg = WebIm.message.create(option)
    WebIm.conn.send(Msg).then(() => {
        console.log(`${ID}`)
        //id id msg  time  from  to  chatType 
        store.commit("setCharArr", Msg)
    }).catch((e) => {
        console.log("", e)
    })
}

