export let getUserInfo = (id, num) => {
    //id
    WebIm.conn.fetchUserInfoById(id).then((res) => {
        console.log('', res.data)
        if (num === 2) {
            store.commit("getfriendsInfo", Object.values(res.data)[0])
        } else {
            store.commit("getuserInfo", Object.values(res.data)[0])
        }
    })
}

