<template>
  <div>
    <a-modal title="" :visible="visible" :footer="null" @cancel="handleCancel">
      <!--  -->
      <div v-if="!friendsEventStatus">
        <a-empty description="......"/>
      </div>
      <!--  -->
      <div class="msg-box" v-else>
        <ul>
          <li v-for="(t,i) in friendsEventVal" :key="i">
            <!--  -->
            <div class="icon">
              <a-avatar shape="square" size="large" icon="user"/>
            </div>
            <!--  -->
            <div class="text-box">
              <!--  -->
              <h3>{{ t.from }}</h3>
              <!--  -->
              <span>{{ t.status }}</span>
            </div>
            <!--  -->
            <div class="btn-box">
              <a-button type="primary" size="small" @click="agreeAddFunc(t.from)"></a-button>
              <a-button type="danger" size="small" @click="refuseAddFunc(t.from)"></a-button>
            </div>
          </li>
        </ul>
      </div>
    </a-modal>
  </div>
</template>

<script>
import './MessageReceipt.scss'
import {mapGetters, mapState} from "vuex";
import {agreeAdd,refuseAdd} from '@/config/optionsIm'

export default {
  name: "MessageReceipt",
  data() {
    return {
      visible: false,
    }
  },
  computed: {
    ...mapState(['friendsEventVal']),
    ...mapGetters(['friendsEventStatus'])
  },
  props: ['messageStatus'],
  watch: {
    messageStatus(nV) {
      this.visible = nV
    }
  },
  methods: {
    handleCancel() {
      this.visible = false;
      this.$emit("MsgStatus", this.visible)
    },
    //
    agreeAddFunc(userId){
      agreeAdd(userId)
    },
    //
    refuseAddFunc(userId){
      refuseAdd(userId)
    },
  }
}
</script>

